body {
  text-align: center;
}

p {
  cursor: pointer;
  margin: 0;
}

.title {
  margin-top: 20px;
}

.container-todo {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.container-todo__title {
  font-size: 1.8rem;
  margin-right: 10px;
}

.container-todo__input {
  margin-left: 10px;
  margin-top: 7px;
}

.container-todo__button {
  margin-left: 10px;
  position: relative;
  border: 1px solid #4262d8;
  border-radius: 5px;
  padding: 3px 5px;
  font-weight: 500;
}

.container-todo__button:hover {
  background-color: #6e8bf6;
  border: 1px solid #6e8bf6;
  color: white;
  transition: 0.3s;
}

.container-list {
  width: 25vw;
  display: flex;
  flex-direction: column;
  list-style: none;
}

.container-list__item {
  width: 98%;
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  font-weight: 500;
}

.container-list__button {
  margin-left: 10px;
  position: relative;
  border: 1px solid #f83833;
  border-radius: 2px;
  padding: 3px 5px;
  font-weight: 500;
  background-color: #fa7976;
  color: white;
}

.container-list__button:hover {
  background-color: #e8110b;
  border: 1px solid #e8110b;
  color: white;
  transition: 0.3s;
}

.complete {
  text-decoration: line-through;
}
